<template>
	<view class="search">
		<view class="head row bothSide verCenter">
			<view class="row verCenter">
				<text @click="goback" class="arrow iconfont iconback"></text>
				<view class="row keyword verCenter" style="margin-left: 10rpx;">
					<text class="iconfont iconsousuo-01"></text>
					<input type="text" focus="true" placeholder="搜索关键词" @input="onChange" @confirm="confirm" class="uni-input" />
				</view>
			</view>
			<view class="btn" @click="submit">搜索</view>
		</view>
		<view class="list column boxsiz">
			<template v-if="search.length > 0">
				<navigator  hover-class="none" :url="'/pages/singer/vocalConcert?id='+item.id"  class="box row bothSide boxsiz" v-for="(item, index) in search" :key="index">
					<view class="pic"><image lazy-load="true" fade-show="true" :src="item.picture_and_vedio[0].url" mode="aspectFill"></image></view>
					<view class="con">
						<text class="t1">{{ item.title }}</text>
						<text class="t2">{{ item.start_time }}</text>
						<text class="t3">{{ item.address }}</text>
						<view class="text row bothSide verCenter">
							<view class="text-price">
								<text class="t1a">￥</text>
								<text class="t1b">{{ item.price }}</text>
							</view>
						</view>
					</view>
				</navigator>
			</template>
			<template v-else>
				<view class="no-date column rowCenter">
					<view class="bg"></view>
					<text>暂无演出</text>
				</view>
			</template>
		</view>
	</view>
</template>

<script>
import { API } from '@/util/api.js';
export default {
	data() {
		return {
			keyword: '',
			search: []
		};
	},
	onLoad(options) {},
	onShow() {
		
	},
	methods: {
		goback() {
			uni.navigateBack({
				delta: 1
			});
		},
		getData() {
			this.request(API.search, 'GET', { keyword: this.keyword }, true).then(res => {
				if (res.code === 10000) {
					this.search = res.data;
				}
			});
		},
		confirm(e) {
			this.keyword = e.detail.value;
			this.getData();
		},
		submit() {
			this.getData();
		},
		onChange(e) {
			this.keyword = e.detail.value;
		},
		tab(index) {
			
		}
	}
};
</script>

<style scoped lang="scss">
@import '../../assets/css/index/search.scss';
</style>
